<template>
    <div class="add-product-cart">
        <button v-on:click="updateCounter(1)" class="add-cart">ADD TO CART</button>
    </div>
</template>

<script>
export default {

    methods :{
        updateCounter(val){
            this.$store.state.counter +=val
        },
    }
}
</script>

<style lang="sass">

    button.add-cart
        font-family: 'DIN Next LT Pro'
        font-size: 14px
        font-weight: 500
        font-style: normal
        color: #FFFFFF
        width: 125px
        height: 50px
        background-color: #333333
        outline: none
        border: none
        border: 1px solid
        transition: 0.2s
        cursor: pointer
        &:hover
            color: #333333
            background-color: #FFFFFF
            border-color: #333333
</style>